import React, {useState} from 'react';
// import './App.css';
import './App.css';
import './tailwind-styles.css';
import ReactCardFlip from 'react-card-flip';


function Card(props) {
    const [flipped, setFlipped] = useState(false);
    const handleFlipClick = (e) => {
        e.preventDefault();
        setFlipped(!flipped);
    }

    // const badgeItemsHTML = [] // these are for the langauges or small descriptions underneath
    // for (const value of props.badgeItems) {
    //     badgeItemsHTML.push(
    //         <div className="badge">
    //             <span>{value}</span>
    //         </div>  
    //     )
    // }
    return (
        <div>
            <ReactCardFlip isFlipped={flipped} flipDirection="horizontal">
                <div className={`card hover:shadow-xl`} onClick={handleFlipClick}>
                    <img className={`w-full h-32 sm:h-48 ${props.smallImage ? "p-10" : ""} ${props.invertSvg ? "invert-svg" : ""}`} type={props.typeOfImage} src={props.image}  alt={props.altImageText}></img>
                    <div className="m-4">
                        <span className="text-gray-600">{props.title}</span>
                        <span className="block text-gray-500 text-sm">{props.dateRange}</span>
                    </div>
                    <div className={`grid grid-cols-3 bg-gray-800 ${props.threeRows ? "grid-rows-3" : ""} ${props.twoRows ? "grid-rows-2" : ""}`}>
                        {props.badgeItems.map((value) => {
                            return <div className="badge"><span>{value}</span></div>
                        })}
                    </div>
                    {/* <button onClick={handleFlipClick}>Click to flip</button> */}
                </div>

                

                <div>
                    <div className={`card hover:shadow-xl`} onClick={handleFlipClick}>
                        {/* <img className={`w-full h-32 sm:h-48 p-10 ${props.invertSvg ? "invert-svg" : ""}`} type="png" src={props.image}  alt={props.altImageText}></img> */}
                        <div className={`w-full sm:h-auto`}>
                            <span>
                            {props.details.map((value) => {
                                return <div className="font-sans text-white p-3"><span>{value}</span></div>
                            })}
                            </span>
                        </div>
                        
                        
                        <div className="m-4">
                            <span className="text-gray-600">{props.location}</span>
                            <span className="block text-gray-500 text-sm">{props.moreDetails}</span>
                        </div>
                        {/* <div className="grid grid-cols-3 bg-gray-800">
                            {props.badgeItems.map((value) => {
                                return <div className="badge"><span>{value}</span></div>
                            })}
                        </div> */}
                        {/* <button onClick={handleFlipClick}>Click to flip</button> */}
                    </div>
                </div>
            </ReactCardFlip>
        </div>
    )
}

export default Card;